@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.10.5/font/bootstrap-icons.css");
@import url('https://fonts.googleapis.com/css2?family=Bungee+Spice&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bungee+Spice&family=Edu+SA+Beginner:wght@500&display=swap');
body{
  background-image: url('./Assets/peakpx\(2\).jpg');
}
.App {
  text-align: center;
}

#heading{ 
  font-family: 'Bungee Spice', cursive;
  max-height: 100%;
}
.task-list{
  background-color: #464950;
  max-height: 400px;
  font-family: 'Bungee Spice', cursive;
font-family: 'Edu SA Beginner', cursive;
}
.ac-pending{
  box-shadow: 0px 5px 5px rgb(103, 164, 197);
}
.ac-completed{
  box-shadow: 0px 5px 5px rgb(86, 216, 108);
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
