.task-label{
    font-family: monospace;
}
.task-submit{
    font-family: monospace;
    background-color:lawngreen;
    color:black;
}
.bi-plus-circle-fill{
    font-size: larger;
    color:greenyellow;
}
.bi-trash-fill{
    font-size: larger;
    color:rgb(104, 231, 163);
}
.mono{
    font-family: monospace;
}
.bold{
    font-weight: bolder;
}
.forms{
    background-color:rgb(229, 187, 212);
    padding:20px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}
.bi-patch-plus-fill {
    font-size: 5rem;
}
.bi-card-checklist{
    font-size: 5rem;
    color:rgb(255, 234, 94);
}
.add-icon{
    animation-name: Rotate;
    animation-duration: 10s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    color: rgb(219, 219, 219);

}
.alert{
    opacity: 1;
    background-color: aquamarine;
    color:darkblue;
}
@keyframes Rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}